import React, { PureComponent } from 'react'
import styles from './styles.module.scss'

export default class Popup extends PureComponent {
    componentDidMount(){
        document.getElementsByTagName('body')[0].style.overflow = 'hidden';
    }
    componentWillUnmount(){
        document.getElementsByTagName('body')[0].style.overflow = 'auto';
    }
  render() {
      const { youtubeId, close } = this.props;
    return (
        <div className={styles.overlay} onClick={close}>
        <div className={styles.content}>
            {youtubeId &&
            <iframe title="youtube video" width="700" height="394" src={`https://www.youtube.com/embed/${youtubeId}`} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            }
        </div>
    </div>
    )
  }
}

