import React from 'react'
import styles from './styles.module.scss'
import Img from '../img';

export default function FeatureItem({ img, title, text, right, first }) {
    return (
        <div className={styles.container}>
            {!right &&
                <div className={styles.image}>
                    <Img src={img} alt={title} className={`${styles.imgTag} ${!first ? styles.pushup : ''}`} />
                </div>
            }
            <div className={`${styles.content} ${right ? styles.right : ''}`}>
                <div className={`gt-america ${styles.title}`}>
                    {title}
                </div>
                <div className={styles.text}>
                    {text}
                </div>
            </div>
            {right &&
                <div className={styles.image}>
                    <Img src={img} alt={title} className={`${styles.imgTag} ${!first ? styles.pushup : ''}`} />
                </div>
            }
        </div>
    )
}
