import React from 'react'
export default function Img({ src, alt, className }) {
    return (
        <picture>
            <source media="(max-width: 650px)" srcSet={require(`../../assets/images/${src}.jpg`)} />
            <img width="100%" className={className} src={require(`../../assets/images/${src}-2x.jpg`)} alt={alt} />
        </picture>
    )
}
